import React, { Component } from 'react';
import SocialFollow from './SocialFollow';

class Introduction extends Component {
  render() {
    return (
      <div
        style={{
          backgroundImage:
            'url(https://designsrock.org/wp-content/uploads/2019/02/Web-design-wordpress-themes.jpg',
          backgroundRepeat: 'no-repeat',
        }}
      >
        <div className="fix-width">
          <div className="row banner-text">
            <div className="col-lg-5 m-t-40">
              <h1>
                HELLO, I'M
                <br />
                <span
                  className="text-info display-3"
                  style={{ marginLeft: '-6px' }}
                >
                  REY AQUINO
                </span>
              </h1>
              <h4 className="font-500">Frontend &amp; Backend Developer</h4>
              <div className="m-t-20">
                <SocialFollow />
              </div>
              <div className="down-btn m-t-40">
                <a
                  href="mailto:includeiostreamsharp@gmail.com"
                  className="btn btn-info btn-rounded m-b-10"
                >
                  CONTACT ME
                </a>
                <a
                  href="https://drive.google.com/file/d/1B6rGAGYwODXPX5LszrzL68U5I5CAj1KO/view?usp=sharing"
                  className="btn btn-success btn-rounded m-b-10"
                >
                  SEE MY RESUME
                </a>
              </div>
            </div>
            <div className="col-lg-7">
              <div className="hero-banner">
                {/* <img
                  src={process.env.PUBLIC_URL + '/images/banner.jpg'}
                  alt="Material Pro admin template"
                /> */}
              </div>
            </div>
          </div>
        </div>
      </div>
    );
  }
}

export default Introduction;
