import React, { Component } from 'react';
import SocialFollow from './SocialFollow';

class Contact extends Component {
  render() {
    return (
      <div>
        <div className="col-md-12 call-to-action test">
          <div className="fix-width">
            <div className="row">
              <div className="col-md-12 m-t-20 m-b-20 text-center">
                <span>Have Any Project In Mind?</span>
              </div>
            </div>
          </div>
        </div>
        <div className="fix-width">
          <div id="contact" className="row contact-text">
            <div className="row col-lg-12 m-t-40 contact-btn">
              <div className="col-md-7 text-center">
                <h1 className="h1-contact">Reach Out to me!</h1>
                <small className="small-contact">
                  DISCUSS A PROJECT OR JUST WANT TO SAY HI? MY INBOX IS OPEN FOR
                  ALL.
                </small>
                <div className="m-t-20">
                  <SocialFollow />
                </div>
                <div className="m-t-40">
                  <small className="small-contact">
                    Makati City, Philippines
                  </small>
                  <h4 className="contact-h4">Open for opportunities: Yes</h4>
                </div>
                <div className="m-b-20">
                  <a
                    href="mailto:includeiostreamsharp@gmail.com"
                    className="btn btn-info btn-rounded m-b-10"
                  >
                    CONTACT ME
                  </a>
                </div>
              </div>
              <div className="col-md-5 text-center img-responsive hero-banner">
                <img
                  className="img-responsive img-circle test1"
                  src={process.env.PUBLIC_URL + '/images/profile2.jpg'}
                  alt="user"
                />
              </div>
            </div>
          </div>
        </div>
      </div>
    );
  }
}

export default Contact;
