import React from 'react';
import { FontAwesomeIcon } from '@fortawesome/react-fontawesome';
import {
  faFacebook,
  faLinkedin,
  faGithub,
  // faTwitter,
} from '@fortawesome/free-brands-svg-icons';

function SocialFollow() {
  return (
    <div className="down-btn" style={{ marginLeft: '-11px' }}>
      <a
        href="https://www.facebook.com/rey.aquino.969952"
        className="facebook social"
      >
        <FontAwesomeIcon icon={faFacebook} size="3x" />
      </a>

      <a
        href="https://www.linkedin.com/in/rey-aquino-39000618a/"
        className="linkedin social"
      >
        <FontAwesomeIcon icon={faLinkedin} size="3x" />
      </a>

      <a href="https://github.com/rey-aquino" className="github social">
        <FontAwesomeIcon icon={faGithub} size="3x" />
      </a>

      {/* <a href="https://www.google.com/" className="twitter social">
        <FontAwesomeIcon icon={faTwitter} size="3x" />
      </a> */}
    </div>
  );
}

export default SocialFollow;
